
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

table{
  border-collapse: collapse;
  border: solid 2px rgb(82, 5, 5);
}
.item{
  width: 20px;
  height: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: rgb(4, 4, 37);
}
.item-active{
  background-color: rgb(134, 7, 7);
}
.item-block{
  background-color: rgb(196, 112, 112);
}
.border{
   visibility: hidden;
}
.modal{
  background-color: rgb(240, 203, 102);
  padding: 200px;
  position: absolute;
  z-index: 3;
  -webkit-transform-origin: center;
          transform-origin: center;
  top: 20vh;
  left: 30%;
}
.filter{
  background-color: #fff;
  opacity: 0.8;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100vh;
}

